<template>
  <table class="pdf-table fs-12 w-100 pdf-table-freight mt-2 text-center" :class="`fs-${fs_type}-18`">
    <thead>
      <tr class="br-r-child-none">
        <th width="7%">{{ $t('PDF_LISTING.index') }}</th>
        <th width="10%">{{ $t('PDF_LISTING.product') }}{{ $t('PDF_LISTING.num') }}</th>
        <th width="31%">{{ $t('PDF_LISTING.named') }}</th>
        <th width="13.24%">{{ $t('PDF_LISTING.quantity') }}</th>
        <th width="11.76%">{{ $t('PDF_LISTING.price') }}</th>
        <th width="7%">{{ $t('PDF_LISTING.discount') }}</th>
        <th width="10%">{{ $t('PDF_LISTING.money') }}</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(v, i) in lising_content" :key="v.id">
        <td>{{ i + 1 }}</td>
        <td>{{ v.num }}</td>
        <td class="text-left pl-2">{{ v.named }}</td>
        <td>
          <div style="width: 60%" class="text-right float-left">{{ v.count }}&nbsp;</div>
          <div style="width: 40%" class="text-left float-left">{{ unit(v.unit) }}</div>
        </td>
        <td class="text-right pr-2">{{ v.price }}</td>
        <td>&nbsp;</td>
        <td class="text-right pr-2">{{ (parseFloat(v.price) * parseFloat(v.count)).toFixed(2) }}</td>
      </tr>

      <tr class="pdf-table-freight-footer br-t-pdf">
        <td>{{ $t('PDF_FOOTER.mark') }}</td>
        <td colspan="3" rowspan="4" class="mark-td">
          <textarea v-model="mark" v-if="_plus" class="pif-textarea pif-textarea-fix" :placeholder="$t('PDF_FOOTER.default_mark')">

          </textarea>
          <div v-else class="pif-textarea text-left">
            {{ show_mark }}
          </div>
        </td>
        <td class="footer-title-td">{{ $t('PDF_FOOTER.total') }}</td>
        <td>HK$</td>
        <td class="text-right pr-2">{{ result_price }}</td>
      </tr>
      <tr class="pdf-table-freight-footer">
        <td></td>
        <td class="footer-title-td">{{ $t('PDF_FOOTER.surcharge') }}</td>
        <td>HK$</td>
        <td class="text-right">
          <input v-model="add" placeholder="0" class="pdf-footer-input" v-if="_plus">
          <span v-else class="pr-2">{{ show_add }}</span>
        </td>
      </tr>
      <tr class="pdf-table-freight-footer">
        <td></td>
        <td class="footer-title-td">{{ $t('PDF_FOOTER.discount') }}</td>
        <td></td>
        <td class="text-right">

          <input v-model="discount" placeholder="(10 = 100%)" class="pdf-footer-input" v-if="_plus">
          <span v-else class="pr-2">{{ show_discount }}</span>

        </td>
      </tr>
      <tr class="pdf-table-freight-footer">
        <td></td>
        <td class="footer-title-td">{{ $t('PDF_FOOTER.total_price') }}</td>
        <td>HK$</td>
        <td class="text-right pr-2">{{ total }}</td>
      </tr>

    </tbody>
  </table>
</template>

<script>
import util from '@/common/es/view'
import tool from '@/common/es/tool'
export default {
  name: "PdfInvoiceFreight",
  props: {
    lising_content: {
      type: Array
    },
    show_mark: {
      type: String
    },
    show_add: {
      type: Number
    },
    show_discount: {
      type: Number
    },
    
    fs_type: {
      type: String,
      default: 'em'
    },
    _plus: {
      type: Boolean
    }
  },
  data() {
    return {
      discount: null,
      add: 10,
      mark: ''
    }
  },
  computed: {
    freight_price() {

    },
    result_price() {
      let res = 0.00
      for (const v of this.lising_content) {
        const freight_price = v.price
        const freight_count = v.count
        const sumR = tool.floatNum.floatMul(freight_price, freight_count)
        res = tool.floatNum.floatAdd(sumR, res)
      }
      return res.toFixed(2)
    },
    total() {
      let res = this.result_price
      if (res) {
        const disc = tool.floatNum.floatMul(this.ser_discount(), 0.1)
        res = tool.floatNum.floatMul(res, disc )
        res = tool.floatNum.floatAdd(res, this.ser_add() )
      }

      return res.toFixed(2)
    }
  },
  methods: {
    unit(u) {
      return util.ser_unit(u)
    },
    ser_discount() {
      let res = this.discount
      if (res) {
        if (isNaN(res)) {
          res = 0
        }

        if (res > 10 || res < 0) { res = 0 }
      } else { res = 0 }

      res = (res == 0) ? 10 : res

      if (!this._plus) {
        res = this.show_discount }

      return res
    },

    ser_add() {
      let res = this.add 
      if (res) {
        if (isNaN(res)) {
          res = 0
        }

        if (res < 0) { res = 0 }
      } else{ res = 0 }

      if (!this._plus) {
        res = this.show_add }

      return res
    },
    locationFooter(res_mark, res_add, res_discount) {
      this.mark = res_mark 
      this.add = res_add
      this.discount = res_discount

      console.log('location disc =', this.discount, ' sd =', this.show_discount)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"

.footer-title-td
  
  
</style>


