<template>
<div>

  <div class="modal fade" tabindex="-1" role="dialog" id="listingPdfViewModal">
    <div class="modal-dialog modal-lg modal-dialog-centered modal-dialog-scrollable">
      <div class="modal-content">
        <div class="modal-header">
          <div>
            <span @click="changeLang(0)" :class="{ 'text-primary': (lang == 0) }" class="hand">中文&nbsp;</span>
            <span class="text-sub">|</span>
            <span @click="changeLang(1)" :class="{ 'text-primary': (lang == 1) }" class="hand">&nbsp;英文</span>
          </div>
          <div>&nbsp;</div>
        </div>
        
        <div class="modal-body pdf_dom" id="pdf_dom">

          <div class="row">
            <div class="col-sm-12 text-center fs-12 mt-2">
              <pdf-header>
                {{ $t('PDF_LISTING.listing') }}
              </pdf-header>
            </div>

            <div class="col-sm-12">
              <table class="pdf-table-outter">
                <tr>
                  <td width="66%" class="br-pdf br-r-none">
                    <pdf-invoice-member 
                      :the_member="membery"
                      :_plus="isPlus"

                      ref="refInvoiceMember"
                      
                      v-if="membery != null"
                    ></pdf-invoice-member>
                  </td>
                  <td class="br-pdf">
                    <pdf-invoice-msg
                      :listing_num="_numed"
                      :listing_time="_timed"
                      :listing_payment="ser_payment(_payment)"
                      :listing_paytime="ser_paytime(_pay_time)"
                      :member_num="membery.num"
                      v-if="membery != null"
                    ></pdf-invoice-msg>
                  </td>
                </tr>
              </table>
            </div>

            <div class="col-sm-12">
              <pdf-invoice-freight 
                :lising_content="_listing_content" 
                :show_add='_add'
                :show_mark='_mark'
                :show_discount='_discount'
                :_plus="isPlus"
                v-if="_listing_content != null"  

                ref="pifREF"
              ></pdf-invoice-freight>
              
            </div>

          </div>
          
          <pdf-footer :member="membery" v-if="membery!=null"></pdf-footer>

        </div>

        <div class="modal-footer fs-12">
          <button class="btn btn-light btn-sm close-btn" data-dismiss="modal">關閉</button>
          <button class="btn btn-outline-info btn-sm print-btn" v-if="isPrint" @click="printPdf">打印 Pdf</button>
          <button class="btn btn-info btn-sm print-btn" v-if="isPrint" @click="savePdf">下載 Pdf</button>

          <button class="btn btn-outline-info btn-sm  print-btn" v-if="isPlus" @click="submit(false)">
            <slot name="buttonText">儲存</slot>
          </button>
          <button class="btn btn-info btn-sm  print-btn" v-if="isPlus" v-show="!isUpdate" @click="submit(true)">
            <slot name="buttonText">儲存並印列</slot>
          </button>
        </div>

      </div>
    </div>
              
    <pdf-vue-html ref="refPdfVueHtml"></pdf-vue-html>

  </div>
  <pdf-iframe ref="pdfIframe"></pdf-iframe>
</div>
</template>

<script>
import conn from '@/service/conn'

import service from '@/service'
import tool from '@/common/es/tool'
import view from '@/common/es/view'
import config from '@/common/config'


import PdfFooter from '@/components/Pdf/PdfFooter.vue'
import PdfHeader from '@/components/Pdf/PdfHeader.vue'

import Loading from '@/components/Shimmer/Loading.vue'
import PdfInvoiceMember from '../../Pdf/Invoice/PdfInvoiceMember.vue'
import PdfInvoiceMsg from '../../Pdf/Invoice/PdfInvoiceMsg.vue'
import PdfInvoiceFreight from '../../Pdf/Invoice/PdfInvoiceFreight.vue'

import PdfIframe from '../../Content/PdfIframe.vue'

import PdfVueHtml from '../../Content/PdfVueHtml.vue'

export default {
  components: { 
    PdfFooter,
    PdfHeader,
  
    Loading,
    PdfInvoiceMember,
    PdfInvoiceMsg,
    PdfInvoiceFreight,

    PdfIframe,
    PdfVueHtml
  },
  name: 'PricesPdf',
  props: {
    _numed: {
      type: String
    },
    _timed: {
      type: String
    },
    _payment: {
      typ: Number
    },
    _pay_time: {
      type: Number
    },
    
    _mark: {
      type: String
    },
    _add: {
      type: Number,
      default: 0
    },
    _discount: {
      type: Number
    },

    membery: {
      type: Object
    },
    
    isPrint: {
      type: Boolean,
      default: false
    },

    isPlus: {
      type: Boolean,
      default: false
    },

    isUpdate: {
      type: Boolean,
      default: false
    },

    _listing_content: {
      type: Array,
      default: null
    },

    _activeId: {
      type: Number,
    }
  },
  async mounted() {
    
    await this.initPrint()

    if (this._add) {

      // console.log('discount =', this._discount)
      this.$refs.pifREF.locationFooter(this._mark, this._add, this._discount)
    }
  },
  data() {
    return {
      isLoading: true,
      lc_id: null,
    }
  },
  computed: {
    lang() {
      let res = this.$i18n.locale
      if (res == 'zh-HK') {
        res = 0
      } else {
        res = 1
      }

      return res
    }
  },
  methods: {
    locationLPFooter(mk, ad, dis) {
      // this.$refs.pifREF.locationFooter(mk, ad, dis)
    },

    changeLang(index) {
      if (index == 0) {
        this.$i18n.locale = 'zh-HK'
      }
      else if (index == 1) {
        this.$i18n.locale = 'en-US'
      }
    },
    
    async nowPrint(lc) {
      this.printOLD(lc)
    },
    async initPrint() {
      let lc = await tool.storage._getAsync('invoice_init_print_lc_id')
      lc = JSON.parse(lc)

      if (lc != 'null' && lc != null && lc != '') {
        
        await tool.storage._setAsync('invoice_init_print_lc_id', '')
        conn.listing.getOne(lc).then(res => {
          
          setTimeout(() => {
            this.printOLD(lc)
          }, 360)
        })
      } else {
        // console.log('无值打印')
      }

    },
    ser_payment(v) {
      const lan = view.ser_lang(this.$i18n.locale)
      return view.ser_listing_paymet(v, lan)
    },
    ser_paytime(v) {
      return view.ser_pay_time(v, this.$i18n.locale)
    },


    savePdf() {
      this.$refs.refPdfVueHtml.print()
    },
    printPdf() {

      this.printOLD(this._activeId)
    },
    submit(need = false) {
      const data = this.$refs.refInvoiceMember.collectData()
      this.$emit('submit_Father', data, need)
    },
    printOLD(id) {
      // alert('print')
      this.lc_id = id
      // const _router = this.$router.resolve({ path: '/pdf/print/?option=listing', query: { lc_id: this.lc_id } })
      // window.open(_router.href, '_blank')
      
      const url = config.baseURL + '/pdf/print/?option=listing&lc_id=' + this.lc_id + '&lang=' + this.$i18n.locale
      
      this.$refs.pdfIframe.printPDF()
      this.$refs.pdfIframe.locationData(true, url)
    },


    collectAdd() {
      let res = this.$refs.pifREF.add 
      if (!res) {
        res = 0
      }
      if (isNaN(res)) {
        res = 0
      }
      return res
    },
    collectMark() {
      let res = this.$refs.pifREF.mark
      if (!res) {
        res = ''
      }
      return res
    },
    collectDiscount() {
      let res = this.$refs.pifREF.ser_discount()
      if (res == 0 || res == 10) {
        res = ''
      }
      return res 
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"


</style>


