const paymetList = [
  '支票',
  '現金'
]
const paymetList_EN = [
  'Cheque',
  'Cash'
]

const payStatus = [
  '未付款',
  '已付款'
]

const payTime = [
  '即付',
  '月結',
  '月結30日',
  '月結45日',
  '月結60日',
  '月結90日',
]
const payTime_EN = [
  'COD',
  'Monthly pay',
  '30 days',
  '45 days',
  '60 days',
  '90 days'
]

const unitList = [
  [0, '斤'],
  [1, '包'],
  [2, '件'],
  [3, '扎'],
  [4, '兩'],
  [5, '磅'],
  [6, '條'],
  [7, '張'],
  [8, '盒'],
  [9, '箱'],
  [10, '隻'],
  [11, '排'],
  [12, '枝'],
  [13, '桶'],
  [14, '個'],
  [15, '次'],
  [16, '樽'],
  [17, '罐'],
  [18, '板'],
  [19, '串'],
  [100, '']
]

module.exports = {
  // 
  unitList,
  payTime,
  payTime_EN,
  paymetList,
  paymetList_EN,
  
  ser_unit (unit) {
    const res = unitList.filter((e) => {
      if (e[0] == unit) { return true } return false
    })
    return res[0][1]
  },
  //
  ser_time (timed) {
    if ((timed == '') || (timed == '') || (timed == '')) {
      return '-'
    }
    return timed
  },
  //
  show_pay_status() {
    return payStatus
  },
  ser_pay_status(pay) {
    if (pay) {
      return '<span class="txt-success">已付款</span>'
    }
    return `<span class="txt-danger">未付款</span>`
  },
  ser_prices_status(draft_status, over_status) {
    if (draft_status) {
      return '<div class="draft">草稿</div>'
    }
    if (over_status) {
      return '已付款'
    }
    return '未付款'
  },
  //
  ser_prices_status_pdf(first_pdf, _html_no, _html_yes) {
    if (first_pdf) {
      return _html_no
    }
    return _html_yes
  },

  ser_lang(lan) {
    let res = 0
    if (lan == 'zh-HK') {
      res = 0
    } else if (lan == 'en-US') {
      res = 1
    }
    return res
  },

  ser_pay_time(id, lan) {
    lan = this.ser_lang(lan)
    if (lan == 1) {
      return payTime_EN[id]
    }
    return payTime[id]
  },
  //
  show_paymet() {
    return paymetList
  },
  ser_listing_paymet(index, lan) {
    if (lan == 1) {
      return paymetList_EN[index]
    }
    return paymetList[index]
  },
  order_num(v) {
    if (v.length > 0) {
      for (const i in v) {
        // console.log(v[i])
      }
    }
  },
  // 建立后台数据
  build_freight_to_prices_content(v) {
    const res = []
    for (const i in v) {
      const f = v[i]
      res.push( { id: f.id, num: f.num, named: f.named, unit: f.unit, price: f.price, } )

    }
    return res
  }
}


