<template>
  <table class="pdf-table w-100" :class="`fs-${fs_type}-18`">
    <tbody >
      <tr>
        <td width="45%">{{ $t('PDF_PRICE.quotation') }}{{ $t('PDF_PRICE.num') }}：</td>
        <td>{{ pricesNum }}</td>
      </tr> 
      <tr>
        <td>{{ $t('PDF_MSG.pay_way') }}：</td>
        <td>{{ ser_pay_way(pricesPayway) }}</td>
      </tr>
      <tr>
        <td>{{ $t('PDF_MSG.pay_time') }}：</td>
        <td>{{ ser_pay_time(pricesPayTime) }}</td>
      </tr>
      <tr>
        <td>{{ $t('PDF_PRICE.quotate') }}{{ $t('PDF_LISTING.date') }}：</td>
        <td>{{ pricesTime }}</td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import tool from '@/common/es/tool'
import view from '@/common/es/view'

export default {
  name: 'PdfPriceMsg',
  props: {
    pricesTime: {
      type: String
    },
    pricesNum: {
      type: String,
      default: ''
    },
    pricesPayTime: {
      type: String,
      default: ''
    },
    pricesPayway: {
      type: String
    },
    member: {
      type: Object
    },
    fs_type: {
      type: String,
      default: 'em'
    }
  },
  data() {
    return {
      
    }
  },
  methods: {
    ser_pay_way() {
      let res = this.pricesPayway

      const lan = view.ser_lang(this.$i18n.locale)

      if (this.pricesPayway) {

          view.paymetList.forEach((e, i) => {
              if (e == this.pricesPayway) {

                if (lan == 1) {
                  res = view.paymetList_EN[i]
                }
              }
          })
      }

      return res
    },

    ser_pay_time() {
      let res = this.pricesPayTime

      const lan = view.ser_lang(this.$i18n.locale)

      if (this.pricesPayTime) {
        
          view.payTime.forEach((e, i) => {
            if (e == this.pricesPayTime) {
              if (lan == 1) {
                res = view.payTime_EN[i]
              }
            }
          })
      }

      return res
    }
  },
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"
</style>


