import { net } from '@/service/api/net.js'
import config from '@/common/config'
import conn_util from '@/common/es/conn_util'

let url = config.baseURL + config.apiURL + '/tag/'
const _url = url 

export default {
  url,
  trash(id) {
    url = _url + id + '/'

    return net({
      url,
      method: 'PUT',
      data: {
        status: false,
        status: 'False'
      }
    })
  },
  delete(id) {
    url = _url + id + '/'

    return net({
      url,
      method: 'DELETE',
    })
  },
  add(data) {
    url = _url
    
    return net({
      url,
      method: 'POST',
      data
    })
  },
  update(data, id) {
    url = _url + id + '/'

    return net({
      url,
      method: 'PUT',
      data
    })
  },
  getOne(id) {
    url = _url + id + '/'

    return net({
      url,
      method: 'GET'
    })
  },
  getList_Tag(condition, status = true) {

    url = _url + conn_util.buildURL(condition, status)

    return net({
      url,
      method: 'get'
    })
  }
}


